import { RouterModule, Routes } from '@angular/router';
import { LoginComponent } from './modules/login/login.component';
import { MainComponent } from './modules/main/main.component'
import { NavigatorComponent } from './modules/navigator/components/navigator.component'
import { NgModule } from '@angular/core';

const appRoutes: Routes = [
  { path: '', loadChildren: () => new Promise(function (resolve) {  (require as any).ensure([], function (require: any) {    resolve(require('./modules/login/login.module')['LoginModule']);  });}), /*, canActivate: [AuthGuard] */ },
  { path: 'application', loadChildren: () => new Promise(function (resolve) {  (require as any).ensure([], function (require: any) {    resolve(require('./modules/main/main.module')['MainModule']);  });}) }, ///*, canActivate: [AuthGuard] */

  // otherwise redirect to home
  { path: '**', redirectTo: '' }
];

@NgModule({
  imports: [
    RouterModule.forRoot(
      appRoutes,
      {
        enableTracing: true // <-- debugging purposes only
        //cd fro preloadingStrategy: SelectivePreloadingStrategy,
      }
    )
  ],
  exports: [
    RouterModule
  ]
  /*providers: [
    CanDeactivateGuard,
    SelectivePreloadingStrategy
  ]*/
})
export class AppRoutingModule { }



